import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const priceData = wutil.makeRandomData({ cnt: 500, min: 0, max: 200, onlyUp: false, inputData: [], diff: 5 });
const aiData = wutil.makeRandomData({ cnt: 500, min: 0, max: 200, onlyUp: false, priceData, diff: 2 });


const initialState = {
	aiData,
	priceData,
};

@observer
class Prices extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { priceData, aiData } = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Prices</h1>
						<LineGraph 
							stores={this.props.stores}
							data={priceData}
							dataB={aiData}
							legend='Prices'
							legendB='AI prediction'
							width={1200}
							height={500}
							yMax={200}
							yMin={0}
							showYTicks={1}
							yTicks='["2 NOK", "1.5 NOK", "1 NOK", "0.5 NOK", "0 NOK"]'
							unit='øre'
							showXTicks={1}
							marker={400}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Prices;
